import * as constants from "./constants";
import {
  getMerchantList,
  switchMerchantState,
} from "../../../../common/api/merchant";
import { message } from "antd";

// tableData
export const setTableData = (data) => ({
  type: constants.SET_TABLE_DATA,
  playload: data,
});

// loading
export const setLoadingState = (val) => ({
  type: constants.SET_LOADING_STATE,
  playload: val,
});

// 
export const setTableLength = (val) => ({
  type: constants.SET_TABLE_LENGTH,
  playload: val,
});

// 
export const getTableData = (pageNum, pageSize, ...args) => {
  return (dispatch) => {
    dispatch(setLoadingState(true));
    getMerchantList(pageNum, pageSize, ...args)
      .then((res) => {
        if (res.data.code === 200) {
          dispatch(setLoadingState(false));
          dispatch(setTableData(res.data.result.list));
          dispatch(setTableLength(res.data.result.total));
        } else {
          message.error("");
        }
      })
      .catch((err) => {
        dispatch(setLoadingState(false));
        message.error("");
      });
  };
};

// 
export const updateMerchantState = (params) => {
  return (dispatch) => {
    switchMerchantState(params)
      .then((res) => {
        if (res.data.code === 200) {
          dispatch(getTableData(1, 10));
          message.success("");
        } else {
          message.error("");
        }
      })
      .catch((err) => {
        console.error(err);
        message.error("");
      });
  };
};
