import React, { Component } from "react";
import {
  Wrapper,
  Title,
  STitle,
  FomBox,
  LogoPrefix,
  InputWrapper,
  OptWrapper,
} from "./style";
import { Button, Checkbox } from "antd";
import { connect } from "react-redux";
import { actionCreators } from "./store";
import { Decrypt } from "../../common/api/ecypto";
import { withRouter } from "react-router-dom";

class Login extends Component {
  constructor(props) {
    super(props);

    this.state = {
      nameFocus: false,
      pwdFocus: false,
      rememberMe: false,
    };

    this.handleNameInputFocus = this.handleNameInputFocus.bind(this);
    this.handleNameInputBlur = this.handleNameInputBlur.bind(this);
    this.handlePwdInputFocus = this.handlePwdInputFocus.bind(this);
    this.handlePwdInputBlur = this.handlePwdInputBlur.bind(this);
    this.handleChecked = this.handleChecked.bind(this);
    this.handleLogin = this.handleLogin.bind(this);
  }

  render() {
    return (
      <Wrapper>
        <div>
          <Title></Title>
          <STitle>pcWeb~</STitle>
          <FomBox as="form">
            <InputWrapper focused={this.state.nameFocus}>
              <LogoPrefix>
                <svg
                  viewBox="64 64 896 896"
                  focusable="false"
                  data-icon="user"
                  width="1em"
                  height="1em"
                  fill="currentColor"
                  aria-hidden="true"
                >
                  <path d="M858.5 763.6a374 374 0 00-80.6-119.5 375.63 375.63 0 00-119.5-80.6c-.4-.2-.8-.3-1.2-.5C719.5 518 760 444.7 760 362c0-137-111-248-248-248S264 225 264 362c0 82.7 40.5 156 102.8 201.1-.4.2-.8.3-1.2.5-44.8 18.9-85 46-119.5 80.6a375.63 375.63 0 00-80.6 119.5A371.7 371.7 0 00136 901.8a8 8 0 008 8.2h60c4.4 0 7.9-3.5 8-7.8 2-77.2 33-149.5 87.8-204.3 56.7-56.7 132-87.9 212.2-87.9s155.5 31.2 212.2 87.9C779 752.7 810 825 812 902.2c.1 4.4 3.6 7.8 8 7.8h60a8 8 0 008-8.2c-1-47.8-10.9-94.3-29.5-138.2zM512 534c-45.9 0-89.1-17.9-121.6-50.4S340 407.9 340 362c0-45.9 17.9-89.1 50.4-121.6S466.1 190 512 190s89.1 17.9 121.6 50.4S684 316.1 684 362c0 45.9-17.9 89.1-50.4 121.6S557.9 534 512 534z"></path>
                </svg>
              </LogoPrefix>
              <input
                placeholder=""
                autoComplete="false"
                className="innerInput"
                onFocus={this.handleNameInputFocus}
                onBlur={this.handleNameInputBlur}
                ref={(input) => (this.username = input)}
              ></input>
            </InputWrapper>
            <InputWrapper focused={this.state.pwdFocus}>
              <LogoPrefix>
                <svg
                  viewBox="64 64 896 896"
                  focusable="false"
                  data-icon="lock"
                  width="1em"
                  height="1em"
                  fill="currentColor"
                  aria-hidden="true"
                >
                  <path
                    d="M832 464h-68V240c0-70.7-57.3-128-128-128H388c-70.7 0-128 57.3-128 128v224h-68c-17.7 0-32 14.3-32 32v384c0 17.7 14.3 32 32 32h640c17.7 0 32-14.3 32-32V496c0-17.7-14.3-32-32-32zM332 240c0-30.9 25.1-56 56-56h248c30.9 0 56 25.1 56 56v224H332V240zm460 600H232V536h560v304z"
                    fill="#1890ff"
                  ></path>
                  <path
                    d="M232 840h560V536H232v304zm280-226a48.01 48.01 0 0128 87v53c0 4.4-3.6 8-8 8h-40c-4.4 0-8-3.6-8-8v-53a48.01 48.01 0 0128-87z"
                    fill="#e6f7ff"
                  ></path>
                  <path
                    d="M484 701v53c0 4.4 3.6 8 8 8h40c4.4 0 8-3.6 8-8v-53a48.01 48.01 0 10-56 0z"
                    fill="#1890ff"
                  ></path>
                </svg>
              </LogoPrefix>
              <input
                placeholder=""
                type="password"
                autoComplete="false"
                className="innerInput"
                onFocus={this.handlePwdInputFocus}
                onBlur={this.handlePwdInputBlur}
                ref={(input) => (this.password = input)}
              ></input>
            </InputWrapper>
            <OptWrapper>
              <Checkbox onClick={this.handleChecked}></Checkbox>
              <span className="right"></span>
            </OptWrapper>
            <Button
              type="primary"
              block
              size="large"
              onClick={() => this.handleLogin(this.props)}
            >
              
            </Button>
          </FomBox>
        </div>
      </Wrapper>
    );
  }

  componentDidMount() {
    let formData = JSON.parse(localStorage.getItem("formStore"));
    if (formData && formData.rememberMe) {
      this.username.value = formData.userName;
      this.password.value = Decrypt(formData.password);
    }
  }

  handleNameInputFocus() {
    this.setState({
      nameFocus: true,
    });
  }
  handleNameInputBlur() {
    this.setState({
      nameFocus: false,
    });
  }
  handlePwdInputFocus() {
    this.setState({
      pwdFocus: true,
    });
  }
  handlePwdInputBlur() {
    this.setState({
      pwdFocus: false,
    });
  }
  handleChecked() {
    let newVal = !this.state.rememberMe;
    this.setState({
      rememberMe: newVal,
    });
  }
  handleLogin(props) {
    let cb = () => {
      let { history } = props;
      console.log("", history);
      history.push("/home", {
        time: Date.now(),
      });
    };
    props.handleLoginIn(
      this.username,
      this.password,
      this.state.rememberMe,
      cb
    );
  }
}

const mapStateToProps = (state) => {
  return {};
};

const mapDispatchToProps = (dispatch) => {
  return {
    handleLoginIn(nameElement, pwdElement, rememberMe, cb) {
      let name = nameElement.value;
      let pwd = pwdElement.value;
      dispatch(
        actionCreators.uLogin(
          {
            userName: name,
            password: pwd,
            rememberMe,
          },
          cb
        )
      );
    },
  };
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Login));
