import * as constants from "./constants";
import { login } from "../../../common/api/user";
import { message } from "antd";
import { Encrypt } from "../../../common/api/ecypto";
import { fromJS } from "immutable";

//
export const setStorage = (data) => ({
  type: constants.SETSTORAGE,
  playload: data,
});

//
export const removeStorage = () => ({
  type: constants.REMOVESTORAGE,
});

// 
export const setUserInfo = (userInfo) => ({
  type: constants.SETUSERINFO,
  playload: userInfo,
});

// token
export const setToken = (token) => ({
  type: constants.SETTOKEN,
  playload: token,
});

// 
export const uLogin = (params, cb) => {
  let formData = { ...params };
  return (dispatch) => {
    login(formData)
      .then((res) => {
        if (res.data.code === 200) {
          dispatch(setUserInfo(fromJS(res.data.result.userVO)));
          dispatch(setToken(fromJS(res.data.result.token)));
          if (params.rememberMe) {
            formData.password = Encrypt(formData.password);
            dispatch(setFormStore(JSON.stringify(formData)));
          } else {
            message.error(res.data.message||'');
            dispatch(removeFormStore());
          }
          cb();
        } else {
          message.error("");
        }
      })
      .catch((err) => {});
  };
};

// 
export const setFormStore = (val) => ({
  type: constants.SETFORMSTORE,
  playload: val,
});

// 
export const removeFormStore = () => ({
  type: constants.REMOVEFORMSTORE,
});

// 
export const logout = () => ({
  type: constants.LOGOUT,
});
