// logo
import React from "react";
import styled from "styled-components";

const Logo = styled.div`
  position: relative;
  display: flex;
  align-items: center;
  padding: 16px;
  line-height: 32px;
  cursor: pointer;
  box-sizing: border-box;
`;

const LogoTitle = styled.h1`
  display: ${(props) => (props.hide ? "none" : "inline-block")};
  height: 32px;
  margin: 0 0 0 12px;
  color: #fff;
  font-weight: 600;
  font-size: 18px;
  line-height: 32px;
  vertical-align: middle;
`;

const TopLogo = (props) => {
  return (
    <Logo>
      <LogoTitle hide={props.collapsed}></LogoTitle>
    </Logo>
  );
};

export default TopLogo;
