import * as constants from "./constants";
import { fromJS } from "immutable";

const defaultState = fromJS({
    provinceList: [],
    cityList: [],
    areaList: [],

    form: {
        merchantName: "",
        nameAbbr: "",
        province: "",
        city: "",
        area: "",
        address: "",
        contactName: "",
        contactPhone: "",
    },

    merchantInfo: {

    }
})

export default (state = defaultState, action) => {
    switch(action.type){
        case constants.SET_PROVINCE_LIST:
            return state.set('provinceList', action.playload);
        case constants.SET_CITY_LIST:
            return state.set('cityList', action.playload);
        case constants.SET_AREA_LIST:
            return state.set('areaList', action.playload);
        case constants.SET_FORM_MERCHANTNAME:
            return state.set('form').set('merchantName', action.playload);
        case constants.SET_MERCHANTINFO:
            return state.set('merchantInfo', action.playload);
        default: return state;
    }
};