import { fromJS } from "immutable";
import * as constants from "./constants";

const defaultState = fromJS({
  roleData: [],
  menuTree: [],
});

export default (state = defaultState, action) => {
  switch (action.type) {
    case constants.SET_ROLE_LIST:
      return state.set("roleData", action.playload);
    case constants.SET_MENU_TREE:
      return state.set("menuTree", action.playload);
    default:
      return state;
  }
};
