import axios from "../request";

// 
export const getLogisticsData = (params) => {
  if (!params) return axios.get("/biz-admin/base/logistics/findByPage");
  let keysArr = Object.keys(params);
  let url = "/biz-admin/base/logistics/findByPage?";
  keysArr.map((key, index) => {
    if (params[key]) {
      if (index === keysArr.length - 1) {
        url += key + "=" + params[key];
      } else {
        url += key + "=" + params[key] + "&";
      }
    }
  });
  return axios.get(url);
};
