import * as constants from "./constants";
import {
  getProviceList,
  getCityList,
  getAreaList,
  getMerchantInfo,
  addMerchantInfo,
} from "../../../../common/api/merchant";
import { message } from "antd";

export const initProvinceList = () => {
  return (dispatch) => {
    getProviceList()
      .then((res) => {
        if (res.data.code === 200) {
          let arr = res.data.result;
          arr.sort((a, b) => a.sort - b.sort);
          dispatch(setProvinceList(res.data.result));
        }
      })
      .catch((err) => {});
  };
};

export const setProvinceList = (data) => ({
  type: constants.SET_PROVINCE_LIST,
  playload: data,
});

export const initCityList = () => {
  return (dispatch) => {
    getCityList()
      .then((res) => {
        if (res.data.code === 200) {
          dispatch(setCityList(res.data.result));
        }
      })
      .catch((err) => {});
  };
};

export const setCityList = (data) => ({
  type: constants.SET_CITY_LIST,
  playload: data,
});

export const initAreaList = () => {
  return (dispatch) => {
    getAreaList()
      .then((res) => {
        if (res.data.code === 200) {
          dispatch(setAreaList(res.data.result));
        }
      })
      .catch((err) => {});
  };
};

export const setAreaList = (data) => ({
  type: constants.SET_AREA_LIST,
  playload: data,
});

export const setMerchantName = (data) => ({
  type: constants.SET_FORM_MERCHANTNAME,
  playload: data,
});

export const getMerchantInfoById = (merchantId) => {
  return (dispatch) => {
    getMerchantInfo(merchantId)
      .then((res) => {
        if (res.data.code === 200) {
          dispatch(setMerchantInfo(res.data.result));
        } else {
          message.warning(res.data.message || "!");
        }
      })
      .catch((err) => {
        message.error(err || "!");
      });
  };
};

export const setMerchantInfo = (data) => ({
  type: constants.SET_MERCHANTINFO,
  playload: data,
});

export const addMerchant = (data, cb) => {
  return dispatch => {
    addMerchantInfo(data).then(res=>{
      if(res.data.code === 200){
        message.success('');
        dispatch(setMerchantInfo(res.data.result));
        cb();
      }else{
        message.warning(res.data.message||'')
      }
    }).catch(err=>{
      console.log(err)
      message.error('');
    })
  }
}
