import React, { Component } from "react";
import { Button, Pagination, Table, Switch, Spin } from "antd";
import {
  Wrapper,
  OptBox,
  FormContainer,
  PageContainer,
  TableContainer,
} from "./style";
import { PlusOutlined } from "@ant-design/icons";
import { connect } from "react-redux";
import { actionCreators } from "./store";
import { formateDateTime } from "../../../common/api/util";
import { Link } from "react-router-dom";

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      pageNum: 1,
      pageSize: 10,
      // 
      columns: [
        {
          title: "",
          dataIndex: "joiningTime",
          key: "joiningTime",
          render: (time) => <>{formateDateTime(time)}</>,
        },
        {
          title: "ID",
          dataIndex: "merchantId",
          key: "merchantId",
        },
        {
          title: "",
          key: "merchantName",
          dataIndex: "merchantName",
        },
        {
          title: "",
          dataIndex: "contactName",
          key: "contactName",
        },
        {
          title: "",
          dataIndex: "contactPhone",
          key: "contactPhone",
        },
        {
          title: "",
          key: "asplace",
          render: (record) => (
            <>
              <span>{record.province + record.city + record.area}</span>
            </>
          ),
        },
        {
          title: "",
          key: "openFlag",
          render: (record) => (
            <>
              <Switch
                checked={record.openFlag}
                onClick={() => this.updateState(record)}
              />
            </>
          ),
        },
        {
          title: "",
          key: "operate",
          render: (record) => (
            <>
              <Link to={"/home/merchant/detail/" + record.merchantId}>
                <Button type="link"></Button>
              </Link>
            </>
          ),
        },
      ],
    };

    this.pageChange = this.pageChange.bind(this);
    this.updateState = this.updateState.bind(this);
  }

  pageChange(pageNum) {
    this.setState({
      pageNum: pageNum,
    });
    this.props.handleGetData(pageNum, this.state.pageSize);
  }

  updateState(record) {
    let params = {
      merchantId: record.merchantId,
      openFlag: !record.openFlag,
    };
    this.props.handleUpdateState(params);
  }

  componentDidMount() {
    this.props.handleGetData(this.state.pageNum, this.state.pageSize);
  }

  render() {
    return (
      <Wrapper>
        <OptBox>
          <Link to={"/home/merchant/settlein"}>
            <Button type="primary" icon={<PlusOutlined />}>
              
            </Button>
          </Link>
        </OptBox>
        <Spin tip="Loading..." spinning={this.props.loading}>
          <FormContainer>
            <TableContainer>
              <Table
                columns={this.state.columns}
                dataSource={this.props.tableData}
                pagination={false}
                rowKey="id"
              />
            </TableContainer>
            <PageContainer>
              <Pagination
                defaultCurrent={this.state.pageNum}
                total={this.props.total}
                showTotal={(total) => `Total ${total} items`}
                onChange={this.pageChange}
              ></Pagination>
            </PageContainer>
          </FormContainer>
        </Spin>
      </Wrapper>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    tableData: state.get("merchantHome").get("tableData"),
    pageNum: state.get("merchantHome").get("pageNum"),
    pageSize: state.get("merchantHome").get("pageSize"),
    total: state.get("merchantHome").get("total"),
    loading: state.get("merchantHome").get("loading"),
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    handleGetData(pageNum, pageSize, ...args) {
      dispatch(actionCreators.getTableData(pageNum, pageSize, ...args));
    },
    handleUpdateState(params) {
      dispatch(actionCreators.updateMerchantState(params));
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Home);
