import styled from "styled-components";
import bgSrc from "../../assets/loginBg.svg";

export const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background-image: url(${bgSrc});
  background-size: cover;
  align-items: center;
  justify-content: center;
`;

export const Title = styled.div`
  color: rgba(0, 0, 0, 0.85);
  font-weight: 600;
  font-size: 33px;
  font-family: Avenir, helvetica neue, Arial, Helvetica, sans-serif;
  text-align: center;
`;

export const STitle = styled.div`
  margin-top: 12px;
  margin-bottom: 20px;
  color: rgba(0, 0, 0, 0.45);
  font-size: 14px;
  text-align: center;
`;

export const FomBox = styled.div`
  width: 368px;
  margin: 0 auto;
  padding: 40px 0;
`;

export const InputWrapper = styled.div`
  padding: 6.5px 11px;
  font-size: 16px;
  position: relative;
  display: inline-block;
  width: 100%;
  min-width: 0;
  color: rgba(0, 0, 0, 0.85);
  line-height: 1.5715;
  background-color: #fff;
  background-image: none;
  border: ${(props) =>
    props.focused ? "1px solid #40a9ff" : "1px solid #d9d9d9"};
  border-radius: 2px;
  transition: all 0.3s;
  display: inline-flex;
  margin-bottom: 24px;
  box-shadow: ${(props) =>
    props.focused ? "0 0 0 2px rgba(24, 144, 255, .2)" : "none"};

  .innerInput {
    border: none;
    outline: none;
    padding: 0;
    box-sizing: border-box;
    margin: 0;
    font-variant: tabular-nums;
    list-style: none;
    font-feature-settings: "tnum", "tnum";
    position: relative;
    display: inline-block;
    width: 100%;
    min-width: 0;
    color: rgba(0, 0, 0, 0.85);
    font-size: 14px;
    line-height: 1.5715;
    background-color: #fff;
    background-image: none;
    border-radius: 2px;
    transition: all 0.3s;
  }
`;

export const LogoPrefix = styled.span`
  display: flex;
  flex: none;
  align-items: center;
  margin-right: 4px;
  color: #1890ff;
`;

export const OptWrapper = styled.div`
  padding: 0 0 20px 0;
  overflow: hidden;

  .right {
    float: right;
    color: #1890ff;
    font-size: 14px;
    cursor: pointer;
  }
`;
