import axios from "../request";

//  
export function getMemberListByParam(params) {
  if (!params) return axios.get("/biz-admin/user/findByPage");
  let keyArr = Object.keys(params);
  if (keyArr.length === 0) return axios.get("/biz-admin/user/findByPage");
  let url = "/biz-admin/user/findByPage?";
  keyArr.map((key, index) => {
    if (params[key]) {
      if (index === keyArr.length - 1) {
        url += key + "=" + params[key];
      } else {
        url += key + "=" + params[key] + "&";
      }
    }
  });
  return axios.get(url); 
}
