import { fromJS } from "immutable";
import * as constants from "./constants";

const defaultState = fromJS({
  tableData: [],
  total: 100,
});

export default (state = defaultState, action) => {
  switch (action.type) {
    case constants.SET_TABLE_DATA:
      return state.set("tableData", action.playload);
    case constants.SET_TABLE_LENGTH:
      return state.set("total", action.playload);
    default:
      return state;
  }
};
