import React, { Component } from "react";
import {
  Wrapper,
  OptBox,
  TableContainer,
  DrawerCoantainer,
  CheckWrapper,
} from "./style";
import {
  Button,
  Switch,
  Table,
  Drawer,
  Form,
  Input,
  Checkbox,
  Space,
  message,
} from "antd";
import { PlusOutlined } from "@ant-design/icons";
import { connect } from "react-redux";
import { actionCreator } from "./store";
import { isEmpty } from "../../../common/api/util";

class RoleManager extends Component {
  constructor(props) {
    super(props);
    this.state = {
      dialogTitle: "",
      pickedArr: [],
    };
    this.columns = [
      {
        title: "",
        dataIndex: "roleName",
        key: "roleName",
      },
      {
        title: "",
        dataIndex: "roleRemark",
        key: "roleRemark",
      },
      {
        title: "",
        key: "state",
        render: (record) => {
          return (
            <Switch
              checked={record.openFlag}
              onClick={() => {
                this.props.switchRoleState(record);
              }}
            />
          );
        },
      },
      {
        title: "",
        key: "operate",
        render: (record) => {
          return (
            <>
              <Button type="link" onClick={() => this.openUpdateRole(record)}>
                
              </Button>
              <Button type="link" danger>
                
              </Button>
            </>
          );
        },
      },
    ];
    this.openCreateAccountDrawer = this.openCreateAccountDrawer.bind(this);
    this.closeAccountDrawer = this.closeAccountDrawer.bind(this);
    this.verifyIsChecked = this.verifyIsChecked.bind(this);
    this.handleCheckedChange = this.handleCheckedChange.bind(this);
    this.roleNameChange = this.roleNameChange.bind(this);
    this.roleRemarkChange = this.roleRemarkChange.bind(this);
    this.handleAddRole = this.handleAddRole.bind(this);
    this.openAddRole = this.openAddRole.bind(this);
    this.openUpdateRole = this.openUpdateRole.bind(this);
    this.handleUpdateRole = this.handleUpdateRole.bind(this);
  }

  openCreateAccountDrawer() {
    // 
    this.setState({
      visible: true,
    });
  }

  closeAccountDrawer() {
    // 
    this.setState({
      dialogTitle: "",
      visible: false,
    });
  }

  verifyIsChecked(menuId) {
    let arr = JSON.parse(JSON.stringify(this.state.pickedArr));
    if (arr.length === 0) return false;
    return arr.indexOf(menuId) >= 0;
  }

  handleCheckedChange(menuId) {
    let arr = JSON.parse(JSON.stringify(this.state.pickedArr));
    let index = arr.findIndex((one) => one === menuId);
    if (index >= 0) {
      arr.splice(index, 1);
    } else {
      arr.push(menuId);
    }
    this.setState({
      pickedArr: arr,
    });
  }

  roleNameChange(e) {
    this.roleName = e.target.value;
  }

  roleRemarkChange(e) {
    this.roleRemark = e.target.value;
  }

  handleAddRole() {
    let params = {
      roleName: this.roleName,
      roleRemark: this.roleRemark,
      menuIds: this.state.pickedArr,
    };
    if (isEmpty(params)) {
      message.warning("");
      return false;
    }
    this.props.handleAddRole(params);
    this.closeAccountDrawer();
  }

  handleUpdateRole() {
    let params = {
      ...this.record,
      roleName: this.roleName,
      roleRemark: this.roleRemark,
      menuIds: this.state.pickedArr,
    }
    if (isEmpty(params)) {
      message.warning("");
      return false;
    }
    this.props.handleAddRole(params);
    this.closeAccountDrawer();
  }

  openAddRole() {
    this.dialogType = "add";
    this.setState({
      dialogTitle: "",
    });
    this.openCreateAccountDrawer();
  }

  openUpdateRole(record) {
    this.dialogType = "edit";
    this.roleName = record.roleName;
    this.roleRemark = record.roleRemark;
    this.setState({
      dialogTitle: "",
    });
    this.record = { ...record };
    this.openCreateAccountDrawer();
  }

  componentDidMount() {
    let param = {
      pageNum: 1,
      pageSize: 10,
    };
    this.props.handleGetTableData(param);
    this.props.handleInitMenuTree();
  }

  render() {
    return (
      <Wrapper>
        <OptBox>
          <Button
            type="primary"
            icon={<PlusOutlined />}
            onClick={this.openAddRole}
          >
            
          </Button>
        </OptBox>
        <TableContainer>
          <Table
            columns={this.columns}
            dataSource={this.props.tableData}
            rowKey="roleId"
          ></Table>
        </TableContainer>
        <Drawer
          title={this.state.dialogTitle}
          closable={true}
          width="512"
          visible={this.state.visible}
          onClose={this.closeAccountDrawer}
          footer={
            <div
              style={{
                textAlign: "right",
              }}
            >
              <Space>
                <Button onClick={this.closeAccountDrawer}></Button>
                {this.dialogType === "add" ? (
                  <Button type="primary" onClick={this.handleAddRole}>
                    
                  </Button>
                ) : (
                  <></>
                )}
                {this.dialogType === "edit" ? (
                  <Button type="primary" onClick={this.handleUpdateRole}>
                    
                  </Button>
                ) : (
                  <></>
                )}
              </Space>
            </div>
          }
        >
          <DrawerCoantainer>
            <Form name="basic" labelAlign="right" labelCol={{ span: 6 }}>
              <Form.Item
                label=""
                name="roleName"
                rules={[
                  { required: true, message: "Please input your roleName!" },
                ]}
              >
                <Input
                  allowClear
                  placeholder=""
                  onChange={(e) => {
                    e.persist();
                    this.roleNameChange(e);
                  }}
                />
              </Form.Item>
              <Form.Item
                label=""
                name="roleRemark"
                rules={[
                  { required: true, message: "Please input your roleRemark!" },
                ]}
              >
                <Input.TextArea
                  allowClear
                  showCount
                  maxLength={100}
                  placeholder=""
                  onChange={(e) => {
                    e.persist();
                    this.roleRemarkChange(e);
                  }}
                />
              </Form.Item>
              <Form.Item label="">
                {this.props.treeMenu.map((item) => {
                  return (
                    <CheckWrapper key={item.menuId}>
                      <Checkbox
                        checked={this.verifyIsChecked(item.menuId)}
                        onChange={() => this.handleCheckedChange(item.menuId)}
                      >
                        {item.menuName}
                      </Checkbox>
                    </CheckWrapper>
                  );
                })}
              </Form.Item>
            </Form>
          </DrawerCoantainer>
        </Drawer>
      </Wrapper>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    tableData: state.get("roleManager").get("roleData"),
    treeMenu: state.get("roleManager").get("menuTree"),
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    handleGetTableData(params) {
      dispatch(actionCreator.getRoleTableData(params));
    },
    switchRoleState(record) {
      let params = {
        roleId: record.roleId,
        openFlag: !record.openFlag,
      };
      dispatch(actionCreator.updateRoleState(params));
    },
    handleInitMenuTree() {
      dispatch(actionCreator.getMenuTreeList());
    },
    handleAddRole(params) {
      dispatch(actionCreator.updateRoleInfo(params));
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(RoleManager);
