import * as constants from "./constants";
import { fromJS } from "immutable";

const defaultState = fromJS({
  userInfo: {},
  token: null,
});

export default (state = defaultState, action) => {
  switch (action.type) {
    case constants.SETSTORAGE:
      let data = action.playload || {};
      let keyArr = Object.keys(data);
      keyArr.map((key) => localStorage.setItem(key, data[key]));
      return state;
    case constants.REMOVESTORAGE:
      localStorage.clear();
      return state;
    case constants.SETUSERINFO:
      localStorage.setItem("userInfo", JSON.stringify(action.playload));
      return state.set("userInfo", action.playload);
    case constants.SETTOKEN:
      let token = action.playload;
      localStorage.setItem("token", token);
      return state.set("token", token);
    case constants.REMOVEFORMSTORE:
      localStorage.removeItem("formStore");
      return state;
    case constants.SETFORMSTORE:
      localStorage.setItem("formStore", action.playload);
      return state;
    case constants.LOGOUT:
      localStorage.removeItem("userInfo");
      localStorage.removeItem("token");
      return state.set("userInfo", {}).set("token", null);
    default:
      return state;
  }
};
