import React, { Component } from "react";
import { Wrapper, OptBox, TableContainer } from "./style";
import { Button, Switch, Table, Space } from "antd";
import { PlusOutlined } from "@ant-design/icons";
import { connect } from "react-redux";
import { actionCreator } from "./store";

class MemberManager extends Component {
  constructor(props) {
    super(props);
    this.state = {};
    this.columns = [
      {
        title: "",
        key: "userName",
        dataIndex: "userName",
      },
      {
        title: "",
        key: "realName",
        dataIndex: "realName",
      },
      {
        title: "",
        key: "mobile",
        dataIndex: "mobile",
      },
      {
        title: "",
        key: "state",
        render: () => {
          return <Switch />;
        },
      },
      {
        title: "",
        key: "operate",
        render: () => {
          return (
            <Space>
              <Button type="link"></Button>
              <Button type="link"></Button>
              <Button type="link"></Button>
              <Button type="link" danger>
                
              </Button>
            </Space>
          );
        },
      },
    ];
    this.pageNum = 1;
    this.pageSize = 10;
  }

  componentDidMount() {
    this.props.handleGetMemberList();
  }

  render() {
    return (
      <Wrapper>
        <OptBox>
          <Button type="primary" icon={<PlusOutlined />}>
            
          </Button>
        </OptBox>
        <TableContainer>
          <Table
            columns={this.columns}
            dataSource={this.props.tableData}
            rowKey="roleId"
          ></Table>
        </TableContainer>
      </Wrapper>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    tableData: state.get("memberManager").get("memberList"),
    total: state.get("memberManager").get("total"),
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    handleGetMemberList() {
      dispatch(actionCreator.getMemberList());
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(MemberManager);
