import LeftMenu from "../../component/menu";
import THeader from "../../component/header";
import React from "react";
import { Switch, Route } from "react-router-dom";
import NotFound from "../404";
import Merchant from "../merchant";
import RoleManager from "../role/roleManager";
import MemberManager from "../role/memberManager";
import Logistics from "../basicData/logistics"
import {
  BasicLayOut,
  SessionLayout,
  MainContent,
  SecondRouterWrapper,
} from "./style";

const MainHome = function (props) {

  return (
    <BasicLayOut>
      <SessionLayout>
        <LeftMenu />
        <MainContent>
          <THeader></THeader>
          <SecondRouterWrapper as="section">
            <Switch>
              <Route
                exact
                path="/home/roleManager"
                component={RoleManager}
              ></Route>
              <Route
                exact
                path="/home/memberManager"
                component={MemberManager}
              ></Route>
              <Route
                path="/home/basedata/logistics"
                component={Logistics}
              ></Route>
              <Route path="/home/merchant" component={Merchant}></Route>
              <Route path="/home" component={Merchant}></Route>
              <Route component={NotFound}></Route>
            </Switch>
          </SecondRouterWrapper>
        </MainContent>
      </SessionLayout>
    </BasicLayOut>
  );
};

export default MainHome;
