export const formateDateTime = (inputTime, flag) => {
  let date = inputTime ? new Date(inputTime) : new Date();
  let y = date.getFullYear();
  let M = date.getMonth() + 1;
  let d = date.getDate();
  let h = date.getHours();
  let m = date.getMinutes();
  let s = date.getSeconds();
  M = M < 10 ? "0" + M : M;
  d = d < 10 ? "0" + d : d;
  h = h < 10 ? "0" + h : h;
  m = m < 10 ? "0" + m : m;
  s = s < 10 ? "0" + s : s;
  if (flag) {
    return y + "-" + M + "-" + d + " " + h + ":" + m + ":" + s;
  } else {
    return y + "-" + M + "-" + d;
  }
};

export const throttle = (fn, interval) => {
  var _self = fn, //
    timer, //
    firstTime = true; //

  return function () {
    var args = arguments,
      _me = this;

    if (firstTime) {
      //
      _self.apply(_me, args);
      return (firstTime = false);
    }

    if (timer) {
      //
      return false;
    }

    timer = setTimeout(function () {
      clearTimeout(timer);
      timer = null;
      _self.apply(_me, args);
    }, interval || 500);
  };
};

export const isEmpty = (data) => {
  if (typeof data === "object") {
    if (data instanceof Array) {
      if (data.length === 0) {
        return true;
      } else {
        return false;
      }
    } else {
      let keysArr = Object.keys(data);
      let flag = false;
      keysArr.map((key) => {
        if (data[key] === undefined || data[key] === null || data[key] === "") {
          flag = true;
        }
      });
      return flag;
    }
  } else {
    if (data === undefined || data === null || data === "") {
      return true;
    } else {
      return false;
    }
  }
};
