import React, { Component } from "react";
import { FormDiv, Title, FormContent, BlockSpan } from "./style";
import { Form, Input, Button, Select, message } from "antd";
import { connect } from "react-redux";
import { actionCreators } from "./store";
import { isEmpty } from "../../../common/api/util";

const validateMessages = {
  // eslint-disable-next-line no-template-curly-in-string
  required: "${label} is required!",
};

const { Option } = Select;
const onFinish = (values) => {
  console.log(values);
};

class MerchantInfo extends Component {
  constructor(props) {
    super(props);
    this.handleProvinceChange = this.handleProvinceChange.bind(this);
    this.handleCityChange = this.handleCityChange.bind(this);
    this.handleAreaChange = this.handleAreaChange.bind(this);
    this.submitForm = this.submitForm.bind(this);
    this.merchantNameChange = this.merchantNameChange.bind(this);
    this.nameAbbrChange = this.nameAbbrChange.bind(this);
    this.addressChange = this.addressChange.bind(this);
    this.contactNameChange = this.contactNameChange.bind(this);
    this.contactPhoneChange = this.contactPhoneChange.bind(this);
    this.form = {
      merchantName: "",
      nameAbbr: "",
      address: "",
      province: "",
      city: "",
      area: "",
      contactName: "",
      contactPhone: "",
    };
  }

  handleProvinceChange(value) {
    //  - 
    let provinceItem = this.props.provinceList.find(
      (item) => item.id === value
    );
    this.form.province = provinceItem.name;
    let newArr = [];
    this.props.cityList.map((item) => {
      if (item.parentId === value) {
        newArr.push(item);
      }
    });
    this.props.handleSetCityList(newArr);
  }

  handleCityChange(value) {
    //  - 
    let cityItem = this.props.cityList.find((item) => item.id === value);
    this.form.city = cityItem.name;
    let newArr = [];
    this.props.areaList.map((item) => {
      if (item.parentId === value) {
        newArr.push(item);
      }
    });
    this.props.handleSetAreaList(newArr);
  }

  handleAreaChange(value) {
    let areaItem = this.props.areaList.find((item) => item.id === value);
    this.form.area = areaItem.name;
  }

  merchantNameChange(e) {
    this.form.merchantName = e.target.value;
  }

  nameAbbrChange(e) {
    this.form.nameAbbr = e.target.value;
  }

  contactNameChange(e) {
    this.form.contactName = e.target.value;
  }

  contactPhoneChange(e) {
    this.form.contactPhone = e.target.value;
  }

  addressChange(e) {
    this.form.address = e.target.value;
  }

  submitForm() {
    //
    console.log("", this.form);
    if (isEmpty(this.form)) {
      message.warning("");
    } else {
      this.props.handleAddMerchantInfo(this.form, () => {
        this.props.setCheckValue(0, true);
        this.props.pickNode(1);
      });
    }
  }

  componentDidMount() {
    if (this.props.merchantId) {
      this.props.handleInitMerchantInfo(this.props.merchantId);
    } else {
      this.props.initProviceList();
      this.props.initCityList();
      this.props.initAreaList();
    }
  }

  render() {
    if (this.props.currentStep !== 0) {
      return <></>;
    } else {
      if (this.props.merchantId) {
        return (
          <>
            <p>{this.props.merchantId}</p>
          </>
        );
      } else {
        return (
          <FormDiv>
            <Title></Title>
            <FormContent>
              <Form
                validateMessages={validateMessages}
                onFinish={onFinish}
                layout="horizontal"
                labelCol={{ span: 6 }}
                wrapperCol={{ span: 14 }}
              >
                <Form.Item
                  name="merchantName"
                  label=""
                  rules={[{ required: true }]}
                >
                  <Input
                    onChange={(e) => {
                      e.persist();
                      this.merchantNameChange(e);
                    }}
                    allowClear
                  />
                </Form.Item>
                <Form.Item
                  label=""
                  name="nameAbbr"
                  rules={[{ required: true }]}
                >
                  <Input
                    onChange={(e) => {
                      e.persist();
                      this.nameAbbrChange(e);
                    }}
                  />
                </Form.Item>
                <Form.Item label="" required>
                  <Form.Item name="province" noStyle>
                    <Select
                      rules={[{ required: true }]}
                      style={{ width: 120 }}
                      placeholder=""
                      allowClear
                      name="province"
                      onChange={this.handleProvinceChange}
                    >
                      {this.props.provinceList.map((item) => {
                        return (
                          <Option value={item.id} key={item.id}>
                            {item.name}
                          </Option>
                        );
                      })}
                    </Select>
                  </Form.Item>
                  <BlockSpan></BlockSpan>
                  <Form.Item name="city" noStyle>
                    <Select
                      rules={[{ required: true }]}
                      style={{ width: 120 }}
                      placeholder=""
                      allowClear
                      name="city"
                      onChange={this.handleCityChange}
                    >
                      {this.props.cityList.map((item) => {
                        return (
                          <Option value={item.id} key={item.id}>
                            {item.name}
                          </Option>
                        );
                      })}
                    </Select>
                  </Form.Item>
                  <BlockSpan></BlockSpan>
                  <Form.Item name="area" noStyle>
                    <Select
                      rules={[{ required: true }]}
                      style={{ width: 120 }}
                      placeholder=""
                      allowClear
                      name="area"
                      onChange={this.handleAreaChange}
                    >
                      {this.props.areaList.map((item) => {
                        return (
                          <Option value={item.id} key={item.id}>
                            {item.name}
                          </Option>
                        );
                      })}
                    </Select>
                  </Form.Item>
                </Form.Item>
                <Form.Item label="" name="address">
                  <Input.TextArea
                    onChange={(e) => {
                      e.persist();
                      this.addressChange(e);
                    }}
                  />
                </Form.Item>
                <Form.Item
                  label=""
                  name="contactName"
                  rules={[{ required: true }]}
                >
                  <Input
                    onChange={(e) => {
                      e.persist();
                      this.contactNameChange(e);
                    }}
                  />
                </Form.Item>
                <Form.Item
                  label=""
                  name="contactPhone"
                  rules={[{ required: true }]}
                  onChange={(e) => {
                    e.persist();
                    this.contactPhoneChange(e);
                  }}
                >
                  <Input />
                </Form.Item>
                <Form.Item label="">
                  <Button type="primary" onClick={this.submitForm}>
                    
                  </Button>
                  <BlockSpan></BlockSpan>
                  <Button></Button>
                </Form.Item>
              </Form>
            </FormContent>
          </FormDiv>
        );
      }
    }
  }
}

const mapStateToProps = (state) => {
  return {
    provinceList: state.get("merchantBase").get("provinceList"),
    cityList: state.get("merchantBase").get("cityList"),
    areaList: state.get("merchantBase").get("areaList"),
  };
};

//  reducer
const mapDispatchToProps = (dispatch) => {
  return {
    initProviceList() {
      dispatch(actionCreators.initProvinceList());
    },
    initCityList() {
      dispatch(actionCreators.initCityList());
    },
    initAreaList() {
      dispatch(actionCreators.initAreaList());
    },
    handleSetCityList(value) {
      dispatch(actionCreators.setCityList(value));
    },
    handleSetAreaList(value) {
      dispatch(actionCreators.setAreaList(value));
    },
    handleInitMerchantInfo(merchantId) {
      dispatch(actionCreators.getMerchantInfoById(merchantId));
    },
    handleAddMerchantInfo(data, cb) {
      dispatch(actionCreators.addMerchant(data, cb));
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(MerchantInfo);
