import * as constants from "./constants";
import {
  roleListByPage,
  addRole,
  updateRole,
  switchRoleState,
  getMenuTree,
} from "../../../../common/api/role";
import { message } from "antd";

export const getRoleTableData = (params) => {
  return (dispatch) => {
    roleListByPage(params)
      .then((res) => {
        if (res.data.code === 200) {
          dispatch(setRoleList(res.data.result.list));
        } else {
          message.warning(res.data.message || "");
        }
      })
      .catch((err) => {
        console.error(err);
        message.warning("");
      });
  };
};

export const setRoleList = (data) => ({
  type: constants.SET_ROLE_LIST,
  playload: data,
});

export const addRoleInfo = (params) => {
  return (dispatch) => {
    addRole(params)
      .then((res) => {
        if (res.data.code === 200) {
          dispatch(getRoleTableData());
          message.success("");
        } else {
          message.warning(res.data.message || "");
        }
      })
      .catch((err) => {
        console.error(err);
        message.error("");
      });
  };
};

