export const getMenuTreeList = () => {
  return (dispatch) => {
    getMenuTree()
      .then((res) => {
        if (res.data.code === 200) {
          dispatch(setMenuTree(res.data.result || []));
        } else {
          message.error(res.data.message || "!");
        }
      })
      .catch((err) => {
        console.error(err);
        message.error("!");
      });
  };
};

export const setMenuTree = (data) => ({
  type: constants.SET_MENU_TREE,
  playload: data,
});
