import { getLogisticsData } from "../../../../common/api/logistics";
import * as constants from './constants'
import {message} from 'antd'

export const getLogisticsDataInfo = (params) => {
    return dispatch => {
        getLogisticsData(params).then(res=>{
            if(res.data.code === 200){
                dispatch(setLogisticsDataInfo(res.data.result.list));
                dispatch(setLogisticsTableLength(res.data.result.total));
            }else{
                message.warning(res.data.message||'!')
            }
        }).catch(err=>{
            console.error(err);
            message.error('!')
        })
    }
}

export const setLogisticsDataInfo = (data) => ({
    type: constants.SET_TABLE_DATA,
    playload: data
})

export const setLogisticsTableLength = (data) => ({
    type: constants.SET_TABLE_LENGTH,
    playload: data
})