import React, { Component } from "react";
import { RoleDiv, Title, RoleContainer, OperateBox, BlockSpan } from "./style";
import { connect } from "react-redux";
import { actionCreators } from "./store";
import { Switch, Table, Button } from "antd";

class RoleConfig extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          title: "",
          dataIndex: "menuName",
          key: "menuId",
        },
        {
          title: "",
          key: "action",
          render: (text, record) => (
            <>
              <Switch
                data-menuid={record.menuId}
                checked={this.props.checkedHasMenu(
                  this.props.merchantMenu,
                  record.menuId
                )}
                onChange={() =>
                  this.props.handleChecked(
                    this.props.merchantMenu,
                    record.menuId
                  )
                }
              />
            </>
          ),
        },
      ],
    };
    this.submitForm = this.submitForm.bind(this);
    this.flag = true;
  }

  submitForm() {
    this.props.handleUpdateMerchantMenuInfo({
      merchantId: this.props.merchantInfo.id,
      menuIds: this.props.merchantMenu
    }, ()=>{
      this.props.setCheckValue(1, true);
      this.props.pickNode(2);
    })
    
  }

  componentDidMount() {
    this.props.initMerchantMeunuList();
  }

  componentDidUpdate() {
    let merchantId = this.props.merchantInfo.id;
    if (merchantId && this.flag) {
      this.props.getMerchantMenu(merchantId);
      this.flag = false;
    }
  }

  render() {
    if (this.props.currentStep !== 1) {
      return <></>;
    } else {
      return (
        <RoleDiv>
          <Title></Title>
          <RoleContainer>
            <Table
              columns={this.state.columns}
              dataSource={this.props.menuList}
              rowKey="menuId"
            />
            <OperateBox>
              <Button></Button>
              <BlockSpan></BlockSpan>
              <Button type="primary" onClick={this.submitForm}>
                
              </Button>
            </OperateBox>
          </RoleContainer>
        </RoleDiv>
      );
    }
  }
}

const mapStateToProps = (state) => {
  return {
    menuList: state.get("merchantConfig").get("menuList"),
    merchantMenu: state.get("merchantConfig").get("merchantMenuList"),
    merchantInfo: state.get("merchantBase").get("merchantInfo"),
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    initMerchantMeunuList() {
      dispatch(actionCreators.getTotalMerchantMenuList());
    },
    getMerchantMenu(id) {
      dispatch(actionCreators.getMerchantMenuInfo(id));
    },
    checkedHasMenu(list, menuId) {
      return list.findIndex((item) => item === menuId) >= 0;
    },
    handleChecked(list, menuId) {
      let newList = [...list];
      let index = list.findIndex((item) => item === menuId);
      if (index >= 0) {
        newList.splice(index, 1);
      } else {
        newList.push(menuId);
      }
      dispatch(actionCreators.setMerchantMenu(newList));
    },
    handleUpdateMerchantMenuInfo(data, cb){
      dispatch(actionCreators.updateMerchantMenuInfo(data, cb));
    }
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(RoleConfig);
