import { reducer as headerReducer } from "../component/header/store";
import { reducer as userReducer } from "../pages/login/store";
import { reducer as merchantHomeReducer } from "../pages/merchant/home/store";
import { reducer as merchantBaseReducer } from "../pages/merchant/base/store";
import { reducer as merchantConfigReducer } from "../pages/merchant/roleConfig/store";
import { reducer as merchantAccountReducer } from "../pages/merchant/account/store";
import { reducer as roleManagerReducer } from "../pages/role/roleManager/store";
import { reducer as memberManagerReducer } from "../pages/role/memberManager/store";
import { reducer as logisticsReducer } from "../pages/basicData/logistics/store";
import { combineReducers } from "redux-immutable";

const reducer = combineReducers({
  header: headerReducer,
  user: userReducer,
  merchantHome: merchantHomeReducer,
  merchantBase: merchantBaseReducer,
  merchantConfig: merchantConfigReducer,
  merchantAccount: merchantAccountReducer,
  roleManager: roleManagerReducer,
  logistics: logisticsReducer,
  memberManager: memberManagerReducer,
});

export default reducer;
