import { Menu } from "antd";
import {
  PieChartOutlined,
  DesktopOutlined,
  ContainerOutlined,
  MailOutlined,
} from "@ant-design/icons";
import React, { Component } from "react";
import styled from "styled-components";
import Logo from "./menuLogo";
import MenuBottom from "./menuBottom";
import { Link } from "react-router-dom";

const MenuContainer = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  height: 100%;
  overflow: hidden;
  flex: ${(props) => (props.hide ? "0 0 80px" : "0 0 208px")};
  max-width: ${(props) => (props.hide ? "80px" : "208px")};
  min-width: ${(props) => (props.hide ? "80px" : "208px")};
  width: ${(props) => (props.hide ? "80px" : "208px")};
  background-color: #001529;
  border-right: 0;
  transition: background-color 0.3s;
  box-sizing: border-box;
`;

const Adapt = styled.div`
  display: flex;
  height: 100%;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
`;

const MenuBlank = styled.div`
  flex: ${(props) => (props.hide ? "0 0 80px" : "0 0 208px")};
  max-width: ${(props) => (props.hide ? "80px" : "208px")};
  min-width: ${(props) => (props.hide ? "80px" : "208px")};
  width: ${(props) => (props.hide ? "80px" : "208px")};
  overflow: hidden;
  box-sizing: border-box;
`;

const SlideWrapper = styled.div`
  overflow: hidden auto;
  height: 100%;

  ::-webkit-scrollbar {
    width: 6px;
    height: 6px;
  }

  ::-webkit-scrollbar-thumb {
    background: hsla(0, 0%, 100%, 0.2);
    border-radius: 3px;
    -webkit-box-shadow: inset 0 0 5px hsla(0, 0%, 100%, 0.05);
  }

  ::-webkit-scrollbar-track {
    background: hsla(0, 0%, 100%, 0.15);
    border-radius: 3px;
    -webkit-box-shadow: inset 0 0 5px rgba(37, 37, 37, 0.05);
  }
`;

const { SubMenu } = Menu;

class LeftMenu extends Component {
  constructor(props) {
    super(props);
    this.state = {
      collapsed: false,
    };
    this.toggleCollapsed = this.toggleCollapsed.bind(this);
  }

  toggleCollapsed = () => {
    this.setState({
      collapsed: !this.state.collapsed,
    });
  };

  render() {
    return (
      <>
        <MenuBlank hide={this.state.collapsed} />
        <MenuContainer hide={this.state.collapsed}>
          <Adapt>
            <Logo collapsed={this.state.collapsed}></Logo>
            <SlideWrapper>
              <Menu
                defaultSelectedKeys={["1"]}
                defaultOpenKeys={["sub0", "sub1", "sub2", "sub3"]}
                mode="inline"
                theme="dark"
                inlineCollapsed={this.state.collapsed}
              >
                <SubMenu
                  key="sub0"
                  icon={<PieChartOutlined />}
                  title=""
                >
                  <Menu.Item key="1">
                    <Link to="/home/merchant"></Link>
                  </Menu.Item>
                  <Menu.Item key="2">
                    <Link to="/home/merchant/settlein"></Link>
                  </Menu.Item>
                  <Menu.Item key="3">
                    <Link to="/home/merchant/detail/0"></Link>
                  </Menu.Item>
                </SubMenu>
                <SubMenu
                  key="sub1"
                  icon={<DesktopOutlined />}
                  title={""}
                >
                  <Menu.Item key="4">
                    <Link to="/home/basedata/logistics"></Link>
                  </Menu.Item>
                  <Menu.Item key="5">
                    <Link to="/home/basedata/area"></Link>
                  </Menu.Item>
                </SubMenu>
                <SubMenu
                  key="sub2"
                  icon={<ContainerOutlined />}
                  title={""}
                >
                  <Menu.Item key="6">
                    <Link to="/home/basedata"></Link>
                  </Menu.Item>
                </SubMenu>
                <SubMenu key="sub3" icon={<MailOutlined />} title="/">
                  <Menu.Item key="7">
                    <Link to="/home/roleManager"></Link>
                  </Menu.Item>
                  <Menu.Item key="8">
                    <Link to="/home/memberManager"></Link>
                  </Menu.Item>
                </SubMenu>
              </Menu>
            </SlideWrapper>
            <MenuBottom
              collapsed={this.state.collapsed}
              toggleCollapsed={this.toggleCollapsed}
            />
          </Adapt>
        </MenuContainer>
      </>
    );
  }
}

export default LeftMenu;
