import { getMemberListByParam } from "../../../../common/api/member";
import * as constants from "./constants";
import { message } from "antd";

export const getMemberList = (params) => {
  return (dispatch) => {
    getMemberListByParam(params)
      .then((res) => {
        if (res.data.code === 200) {
          dispatch(setMemberList(res.data.result.list));
          dispatch(setMemberTotal(res.data.result.total));
        } else {
          message.warning(res.data.message || "");
        }
      })
      .catch((err) => {
        message.error("");
      });
  };
};

export const setMemberList = (data) => ({
  type: constants.SET_MEMBER_LIST,
  playload: data,
});

export const setMemberTotal = (data) => ({
  type: constants.SET_MEMBER_TOTAL,
  playload: data,
});
