import React, { Component } from "react";
import {
  Wrapper,
  OptBox,
  FormContainer,
  PageContainer,
  TableContainer,
  RightBox,
} from "./style";
import { Button, Switch, Table, Input, Pagination } from "antd";
import { PlusOutlined } from "@ant-design/icons";
import { connect } from "react-redux";
import { actionCreator } from "./store";

const { Search } = Input;

class Logistics extends Component {
  constructor(props) {
    super(props);
    this.state = {
      drawerTitle: "/",
    };
    this.drawerType = "add";
    this.pageNum = 1;
    this.pageSize = 10;
    this.allMatchs = ""; // 
    this.columns = [
      {
        title: "/",
        dataIndex: "logisticsName",
        key: "logisticsName",
      },
      {
        title: "/",
        dataIndex: "logisticsCode",
        key: "logisticsCode",
      },
      {
        title: "",
        key: "state",
        render: (record) => (
          <>
            <Switch checked={record.closeSwitch} />
          </>
        ),
      },
      {
        title: "",
        key: "operate",
        render: (record) => (
          <>
            <Button type="link" onClick={() => this.editRecord(record)}>
              
            </Button>
          </>
        ),
      },
    ];
    this.editRecord = this.editRecord.bind(this);
    this.addRecord = this.addRecord.bind(this);
    this.pageChange = this.pageChange.bind(this);
    this.queryTableData = this.queryTableData.bind(this);
    this.searchInputChange = this.searchInputChange.bind(this);
  }

  editRecord() {
    this.setState({
      drawerTitle: "/",
    });
  }

  addRecord() {
    this.setState({
      drawerTitle: "/",
    });
  }

  pageChange(page) {
    this.pageNum = page;
    this.queryTableData();
  }

  searchInputChange(e){
    this.allMatchs = e.target.value;
  }

  queryTableData() {
    let params = {
      pageNum: this.pageNum || 1,
      pageSize: this.pageSize || 10,
      allMatchs: this.allMatchs,
    };
    this.props.handleQueryTableData(params);
  }

  componentDidMount() {
    this.queryTableData();
  }

  render() {
    return (
      <Wrapper>
        <OptBox>
          <Button type="primary" icon={<PlusOutlined />}>
            /
          </Button>
          <RightBox>
            <Search
              placeholder="/"
              style={{ width: 300 }}
              onChange={(e)=>{
                e.persist();
                this.searchInputChange(e);
              }}
              onSearch={this.queryTableData}
            />
          </RightBox>
        </OptBox>
        <FormContainer>
          <TableContainer>
            <Table
              columns={this.columns}
              dataSource={this.props.tableData}
              pagination={false}
              rowKey="logisticsId"
            />
          </TableContainer>
          <PageContainer>
            <Pagination
              defaultCurrent={this.pageNum}
              showSizeChanger={false}
              total={this.props.total}
              showTotal={(total) => `Total ${total} items`}
              onChange={this.pageChange}
            ></Pagination>
          </PageContainer>
        </FormContainer>
      </Wrapper>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    tableData: state.get("logistics").get("tableData"),
    total: state.get("logistics").get("total"),
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    handleQueryTableData(params) {
      dispatch(actionCreator.getLogisticsDataInfo(params));
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Logistics);
