import React, { Component } from "react";
import {
  AccountContainer,
  Title,
  OptBox,
  TableContainer,
  DrawerCoantainer,
} from "./style";
import { Button, Table, Switch, Drawer, Form, Input, Space } from "antd";
import { PlusOutlined } from "@ant-design/icons";
import { connect } from "react-redux";
import { actionCreator } from "./store";

class MerchantAccount extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          title: "",
          dataIndex: "userName",
          key: "userName",
        },
        {
          title: "",
          key: "state",
          render: (record) => (
            <>
              <Switch
                checked={record.openFlag}
                onClick={() => {
                  this.switchAccountState(record);
                }}
              ></Switch>
            </>
          ),
        },
        {
          title: "",
          key: "action",
          render: (record) => (
            <>
              <Button type="link"></Button>
            </>
          ),
        },
      ],
      visible: false,
      dialogTitle: "",
      userName: "",
      password: "",
      confirmPwd: "",
    };

    this.switchAccountState = this.switchAccountState.bind(this);
    this.openCreateAccountDrawer = this.openCreateAccountDrawer.bind(this);
    this.closeAccountDrawer = this.closeAccountDrawer.bind(this);
    this.usernameChange = this.usernameChange.bind(this);
    this.passwordChange = this.passwordChange.bind(this);
    this.confirmPwdChange = this.confirmPwdChange.bind(this);
    this.cancel = this.cancel.bind(this);
  }

  switchAccountState(record) {
    // 
    let merchantId = record.merchantId,
      openFlag = !record.openFlag;

    this.props.switchMerchantAccountState(merchantId, openFlag);
  }

  openCreateAccountDrawer() {
    // 
    this.setState({
      visible: true,
    });
  }

  closeAccountDrawer() {
    // 
    this.setState({
      dialogTitle: "",
      visible: false,
    });
  }

  cancel() {
    //
    this.setState({
      visible: false,
    });
  }

  usernameChange(e) {
    this.setState({
      userName: e.target.value,
    });
  }

  passwordChange(e) {
    this.setState({
      password: e.target.value,
    });
  }

  confirmPwdChange(e) {
    this.setState({
      confirmPwd: e.target.value,
    });
  }

  componentDidMount() {
    this.props.getMerchantAccount();
  }

  render() {
    if (this.props.currentStep !== 2) {
      return <></>;
    } else {
      return (
        <AccountContainer>
          <Title></Title>
          <OptBox>
            <Button
              icon={<PlusOutlined />}
              type="primary"
              onClick={this.openCreateAccountDrawer}
            >
              
            </Button>
          </OptBox>
          <TableContainer>
            <Table
              columns={this.state.columns}
              dataSource={this.props.accountList}
              rowKey="id"
            />
          </TableContainer>
          <Drawer
            title={this.state.dialogTitle}
            closable={true}
            width="512"
            visible={this.state.visible}
            onClose={this.closeAccountDrawer}
          >
            <DrawerCoantainer>
              <Form name="basic" labelAlign="right" labelCol={{ span: 6 }}>
                <Form.Item
                  label=""
                  name="username"
                  rules={[
                    { required: true, message: "Please input your username!" },
                  ]}
                >
                  <Input
                    onChange={(e) => {
                      e.persist();
                      this.usernameChange(e);
                    }}
                    allowClear
                    placeholder=""
                  />
                </Form.Item>

                <Form.Item
                  label=""
                  name="password"
                  rules={[
                    {
                      required: true,
                      message: "Please input your confirm password!",
                    },
                  ]}
                >
                  <Input.Password
                    onChange={(e) => {
                      e.persist();
                      this.passwordChange(e);
                    }}
                    allowClear
                    placeholder=""
                  />
                </Form.Item>

                <Form.Item
                  label=""
                  name="rePassword"
                  rules={[
                    { required: true, message: "Please input your password!" },
                  ]}
                >
                  <Input.Password
                    onChange={(e) => {
                      e.persist();
                      this.confirmPwdChange(e);
                    }}
                    allowClear
                    placeholder=""
                  />
                </Form.Item>

                <Form.Item label="">
                  <Space>
                    <Button onClick={this.cancel}></Button>
                    <Button type="primary"></Button>
                  </Space>
                </Form.Item>
              </Form>
            </DrawerCoantainer>
          </Drawer>
        </AccountContainer>
      );
    }
  }
}

const mapStateToProps = (state) => {
  return {
    accountList: state.get("merchantAccount").get("accountList"),
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    getMerchantAccount() {
      dispatch(actionCreator.getMerchantUserInfo(1012));
    },
    switchMerchantAccountState(merchantId, flag) {
      dispatch(
        actionCreator.switchAccountState({
          merchantId: merchantId,
          openFlag: flag,
        })
      );
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(MerchantAccount);
