// 
axios.interceptors.request.use(function (config) {
  // 

  const token = localStorage.getItem('token');  // redux  storage
 
  if(token) config.headers.Authorization = "Bearer " + token;
  return config;
}, function (error) {
  return Promise.reject(error);
});

