import {
  getMerchantMenuList,
  getMerchantMenu,
  addMerchantMenu,
} from "../../../../common/api/merchant";
import * as constants from "./constants";
import { message } from "antd";

export const getTotalMerchantMenuList = () => {
  return (dispatch) => {
    getMerchantMenuList()
      .then((res) => {
        if (res.data.code === 200) {
          dispatch(setMenuList(res.data.result));
        } else {
          message.warning("");
        }
      })
      .catch((err) => {
        message.error("");
      });
  };
};

export const setMenuList = (data) => ({
  type: constants.SET_ROLE_LIST,
  playload: data,
});

export const getMerchantMenuInfo = (merchantId) => {
  return (dispatch) => {
    getMerchantMenu(merchantId)
      .then((res) => {
        if (res.data.code === 200) {
          let arr = [];
          res.data.result.map((item) => {
            arr.push(item.menuId);
          });
          dispatch(setMerchantMenu(arr));
        } else {
          message.warning("");
        }
      })
      .catch((err) => {
        message.error("");
      });
  };
};

export const setMerchantMenu = (data) => ({
  type: constants.SET_MERCHANT_ROLE,
  playload: data,
});

export const updateMerchantMenuInfo = (data, cb) => {
  return (dispatch) => {
    addMerchantMenu(data)
      .then((res) => {
        if (res.data.code === 200) {
          message.success("");
          cb();
        } else {
          message.warning(res.data.message || "");
        }
      })
      .catch((err) => {
        message.error("");
      });
  };
};
