import React, { Component } from "react";
import { Wrapper, Content, TimeBox, Timespan } from "./style";
import { message, Timeline } from "antd";
import MerchantInfo from "../base";
import RoleConfig from "../roleConfig";
import MerchantAccount from "../account";
import SuccessPage from "../success";

class SettleIn extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentStep: 0,
      stepList: ["", "", "", ""],
      checkValue: [false, false, false], //true
      merchantId: null,
    };

    this.pickTimeNode = this.pickTimeNode.bind(this);
    this.setCheckValue = this.setCheckValue.bind(this);
    this.setMerchantId = this.setMerchantId.bind(this);
  }

  pickTimeNode(index) {
    if (index >= 0 && index <= this.state.stepList.length) {
      if (index === 0) {
        this.setState({
          currentStep: index,
        });
        return;
      }
      if (this.state.checkValue[index - 1] === true) {
        this.setState({
          currentStep: index,
        });
      } else {
        message.info("");
      }
    }
  }

  setCheckValue(idx, val) {
    let arr = [...this.state.checkValue];
    arr[idx] = val;
    this.setState({
      checkValue: arr,
    });
  }

  setMerchantId(val) {
    this.setState({
      merchantId: val,
    });
  }

  render() {
    return (
      <Wrapper>
        <Content>
          <MerchantInfo
            pickNode={this.pickTimeNode}
            setCheckValue={this.setCheckValue}
            setMerchantId={this.setMerchantId}
            currentStep={this.state.currentStep}
          ></MerchantInfo>
          <RoleConfig
            pickNode={this.pickTimeNode}
            setCheckValue={this.setCheckValue}
            currentStep={this.state.currentStep}
          ></RoleConfig>
          <MerchantAccount
            pickNode={this.pickTimeNode}
            currentStep={this.state.currentStep}
          ></MerchantAccount>
          <SuccessPage
            pickNode={this.pickTimeNode}
            currentStep={this.state.currentStep}
          ></SuccessPage>
        </Content>
        <TimeBox>
          <Timeline mode="right">
            {this.state.stepList.map((item, index) => {
              return (
                <Timeline.Item
                  color={index === this.state.currentStep ? "blue" : "gray"}
                  key={index}
                >
                  <Timespan
                    light={index === this.state.currentStep}
                    onClick={() => {
                      this.pickTimeNode(index);
                    }}
                  >
                    {item}
                  </Timespan>
                </Timeline.Item>
              );
            })}
          </Timeline>
        </TimeBox>
      </Wrapper>
    );
  }
}
export default SettleIn;
