import React from "react";
import styled from "styled-components";
import { connect } from "react-redux";
import { actionCreators } from "../../pages/login/store";
import userSrc from "../../assets/user.png";
import { Menu, Dropdown } from "antd";
import { useHistory } from "react-router-dom";

const Title = styled.header`
  position: relative;
  padding: 0px 10px;
  height: 48px;
  line-height: 48px;
  width: 100%;
  z-index: 19;
  background-color: #fff;
  box-shadow: 0 1px 4px rgba(0, 21, 41, 0.08);
  box-sizing: border-box;
`;

const UserBox = styled.div`
  position: absolute;
  right: 20px;
  top: 0;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 12px;
  cursor: pointer;
  transition: all 0.3s;

  &:hover {
    background: rgba(0, 0, 0, 0.025);
  }
`;

const ImgBox = styled.div`
  width: 24px;
  height: 24px;
  flex-shink: 0;
  overflow: hidden;
  margin-right: 10px;

  img {
    display: block;
    width: 100%;
    height: 100%;
  }
`;

const InfoBox = styled.div`
  line-height: 26px;
  color: rgba(0, 0, 0, 0.85);
  font-size: 14px;
`;

const menu = (cb) => (
  <Menu>
    <Menu.Item></Menu.Item>
    <Menu.Item onClick={cb}></Menu.Item>
  </Menu>
);

const THeader = (props) => {
  let history = useHistory();

  function logout() {
    props.handleLogout();
    history.push("/login");
  }

  return (
    <Title>
      <Dropdown overlay={menu(logout)} placement="bottomRight">
        <UserBox>
          <ImgBox>
            <img src={userSrc} alt="user"></img>
          </ImgBox>
          <InfoBox>
            {props.name ||
              (localStorage.getItem("userInfo") &&
                JSON.parse(localStorage.getItem("userInfo"))["userName"]) ||
              ""}
          </InfoBox>
        </UserBox>
      </Dropdown>
    </Title>
  );
};

//  redux  state
const mapStateToProps = (state) => {
  return {
    name: state.getIn(["user", "userInfo", "userName"]),
  };
};

//  reducer
const mapDispatchToProps = (dispatch) => {
  return {
    handleLogout() {
      dispatch(actionCreators.logout());
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(THeader);
